.p-accordion__item{
    margin-bottom: rem(30);
}
 
  
  
  /* 矢印 */
  .p-accordion__title {
    position: relative;
   background: $green;
    color: $white;
    font-weight: $bold;
  }
  .p-accordion__title::after {
    border-right: solid rem(3) $white;
    border-top: solid rem(3) $white;
    content: "";
    display: block;
    height:rem(16);
    position: absolute;
    right: 25px;
    top: 28%;
    transform: rotate(135deg);
    transition: transform .3s ease-in-out, top .3s ease-in-out;
    width:rem(16);
  }
  .p-accordion__title.open::after {
    top: 45%;
    transform: rotate(-45deg);
  }
  
  .p-accordion__item:first-of-type .p-accordion__content{
    display: block;
  }



