@use "global" as *;
.menu{
    height: 20px;
    position: absolute;
    right: 20px;
    top: 32px;
    width: 30px;
    z-index: 10001;
    display: none;
    @include mq(md){
        display: block;
        }
}
.menu__line{
    background: $black;
    display: block;
    height: 2px;
    position: absolute;
    transition:transform .3s;
    width: 100%;
}

.menu__line--center{
    top: 9px;
}
.menu__line--bottom{
    bottom: 0;
}
.menu__line--top.active{
    top: 8px;
    transform: rotate(45deg);
    background: $white;
    
}
.menu__line--center.active{
    transform:scaleX(0);
    background: $white;
    
    
}
.menu__line--bottom.active{
    bottom: 10px;
    transform: rotate(135deg);
    background: $white;
    
    
}

