@use "global" as *;
.p-modal {
    position: fixed;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%,-50%);
    display: none;
    z-index: 501;
  }
  
  @media screen and (min-width: 765px) {
    .p-modal {
      max-width: calc(100% - 25px * 2);
    }
  }
  
  .p-modal__button {
    display: block;
    cursor: pointer;
    // border: 2px solid black;
    text-align: center;
    font-size: 1.25rem;
    font-weight: bold;
    // margin-top: 3.125rem;
    // padding: 1.25rem;
    // color: #ff6ff5;
    width: 50%;
    margin-left:auto;
    margin-right:auto;
   
  }
  

  
  .p-modal__bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 500;
    display: none;
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  .p-modal__wrap {
    border: 4px solid $black;
    background: $black;
    padding: 1.25rem;
    max-height: 1000px;
    overflow: scroll;
    width: 100%;
  }
  .p-modal__box{
      margin: rem(100) 0;
  }